import { FieldSet, Attachment } from 'airtable';
export enum ProductCategory {
	laptops = 'laptops',
	phones = 'phones',
	smart_devices = 'smart devices',
	accessories = 'accessories',
}
export interface Product extends FieldSet {
	id?: string;
	name: string;
	brand: string;
	price: number;
	rating?: number;
	displayimage: ImageAttachment [];
	colors: string;
	details: string;
	category: ProductCategory;
	inventory?: string[];
	usercart?: string[];
}
export interface ImageAttachment extends Attachment {};